*{
    padding: 0px;
    margin: 0px;
    overflow-x: hidden;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}

.content{
    width: 100vw;
    height: 100vh;
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.663);
}
.intro{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: flex;
    flex-direction: column;
    margin-top: -10%;
    margin-left: 100px;
}
.sideInfo{
    color: white;
    padding: 10px;
    font-weight: bolder;
    font-size: 15px;
}
.addressDetail{
    font-size: 35px;
}
.name{
    font-size: 60px;
    font-weight: bolder;
    color: white;
}
.textCont{
    color:white;
    display: flex;
    flex-direction: column;
    margin-top: -10%;
    margin-left: 25%;
}
.specialText{
    font-size: 100px;
    font-weight: bolder;
    border-bottom: 4px solid white;
}
.normalText{
    margin-top: 20px;
    font-size: 18px;
}
@media screen and (max-width: 1300px) {
    .content{
        flex-direction: column;
        align-items: center;
    }
    .intro{
        margin-top: 20%;
        text-align: center;
        margin-left: 0px;
    }
    .textCont{
        margin-top: 10%;
        margin-left: 0px;
    }
}
@media screen and (max-width: 720px) {
    .specialText{
        font-size: 60px;
        text-align: center;
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
    }
    .normalText{
        text-align: center;
        padding: 20px;
    }
    .textCont{
        align-items: center;
    }
}
*{
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}
.header{
    display: flex;
    flex-direction: row;
    background-color: rgba(39, 39, 39, 0.663);
    color: white;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    align-items: center;
    justify-content: space-between;
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);

}
.title{
    font-size: 25px;
    font-weight: bolder;
    padding: 10px;
    padding-left: 30px;
}
.navbar{
    width: 20%;
}
.nav{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-evenly;
}

@media screen and (max-width: 1000px) {
    .navbar{
        width: 40%;
    }
}
*{
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;
}
.content2{
    width: 30%;
    display: flex;
    flex-direction: column;
    color: white;
    height: 50vh;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.414);
    border-radius: 10px;
    margin-top: 10%;
}
.headerText{
    font-size: 50px;
    font-weight: bolder;
    padding: 20px;
}
.contacts{
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 20px;
    font-weight: bolder;
    padding: 20px;
    margin-top: 50px;
    text-align: center;
}

@media screen and (max-width: 1245px) {
    .content2{
        width: 50%;
    }
}


@media screen and (max-width: 830px) {
    .content2{
        width: 100%;
        height: 30vh;
        margin-top: 50%;
    }
}
